.swiper {
    max-width: 22.813rem;
    margin: 0 auto;

    &-slide {
        position: relative;
        width: 16.75rem;
        border-radius: 24px;
    }
}

.swiper2 {

}

.slide-text {
    width: 16.75rem;
}

.swiper-button-prev {
    display: none;
}

.swiper-button-next {
    display: none;
}

@media (min-width: @desktop-width) {
    .swiper {
        max-width: 72.5rem;

        &-slide {
            width: 22.5rem;
            border-radius: 1.5rem;
        }

        &-image {
            width: 22.5rem;
            height: 22.5rem;

            &-overlay {
                position: relative;
                height: 360px;
                border-radius: 36px;
                overflow: hidden;
            }
        }
    }

    .slide-text {
        font-size: 1.25rem;
        line-height: 1.875rem;
        width: 22.5rem;
    }


    .swiper-slide:hover .swiper-image-overlay:after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0 0 0 10000px rgba(122, 31, 193, 0.2);
    }

    .swiper-slide:hover .slide-text {
        color: #6A1BA8;
    }

    .swiper-slide:active .swiper-image-overlay:after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0 0 0 10000px rgba(122, 31, 193, 0.4);

    }

    .swiper-slide:hover .slide-text {
        color: #8A24DB;
    }

    .swiper-button-prev {
        display: flex;
        left: 10px;
    }
    .swiper-button-next {
        display: flex;
        right: 210px;
    }
    .swiper2 {
        position: relative;
        margin: 0 auto;
        overflow: hidden;
        width: 72.5rem;
        padding: 0 100px;
        &-wrapper {
            width: 72.5rem;
            margin: 0 auto;
        }
    }
}


